<template>
  <div id="app">
    <h1>
      <span class="cc-white">Channel</span>
      <span class="cc-green">Coin</span>
    </h1>
    <div id="container">
      <p v-if="message">{{ message }}</p>
      <div v-if="isLoaded">
        <section>
          <details open>
            <summary>
              <h2 style="display: inline-block">Details</h2>
            </summary>
            <table>
              <tr>
                <td>Chain ID</td>
                <td>{{ networkId }}</td>
              </tr>
              <tr>
                <td>ChannelCoin contract address</td>
                <td>
                  <span class="truncate">{{ contractAddress }}</span>
                  <button type="button" @click="metamaskWatchToken">Track in MetaMask 🦊</button>
                </td>
              </tr>
              <tr>
                <td>Your address</td>
                <td>
                  <span class="truncate">{{ account }}</span>
                </td>
              </tr>
              <tr>
                <td>ChannelCoin contract’s own balance</td>
                <td>
                  {{ contractBalance }} CC
                  <button
                    type="button"
                    @click="refreshContractBalance"
                  >Refresh</button>
                </td>
              </tr>
              <tr>
                <td>Your on-chain balance</td>
                <td>
                  {{ actualTokenBalance }} CC
                  <button type="button" @click="refreshBalance">Refresh</button>
                  <button type="button" @click="resetBalance">Set to 100 CC</button>
                </td>
              </tr>
            </table>
          </details>
        </section>
        <section>
          <h2>Balance Receipt &#x1F9FE;</h2>
          <table>
            <tr>
              <td>
                <label for="channel-id">Channel ID:</label>
              </td>
              <td>
                <form v-on:submit.prevent="onSubmit">
                  <input id="channel-id" type="text" v-model="channelId">
                  <button type="submit">Load</button>
                </form>
              </td>
            </tr>
          </table>
          <table>
            <tr>
              <th>Participant №</th>
              <th>Address</th>
              <th>Balance</th>
              <th>Signature</th>
              <th>Valid</th>
              <th></th>
            </tr>
            <tr>
              <td>
                1
                <span v-if="you1">(You)</span>
              </td>
              <td>
                <span class="truncate">{{ participant1 || '—' }}</span>
              </td>
              <td>
                <input type="number" v-model="balance1" min="0" :max="totalBalance"> CC
              </td>
              <td>
                <span
                  class="truncate"
                  :class="{valid: sigVerified1, invalid: !sigVerified1}"
                >{{ sig1 || '—' }}</span>
              </td>
              <td>{{ sigVerified1 ? '✔️' : '❌' }}</td>
              <td>
                <button v-if="you1 && !sigVerified1" type="button" @click="genSig1">Sign 🦊</button>
              </td>
            </tr>
            <tr>
              <td>
                2
                <span v-if="you2">(You)</span>
              </td>
              <td class="truncate">{{ participant2 || '—' }}</td>
              <td>
                <input type="number" v-model="balance2" min="0" :max="totalBalance"> CC
              </td>
              <td>
                <span
                  class="truncate"
                  :class="{valid: sigVerified2, invalid: !sigVerified2}"
                >{{ sig2 || '—' }}</span>
              </td>
              <td>{{ sigVerified2 ? '✔️' : '❌' }}</td>
              <td>
                <button v-if="you2 && !sigVerified2" type="button" @click="genSig2">Sign 🦊</button>
              </td>
            </tr>
          </table>

          <p>
            Receipt Sequence №:
            <input type="number" v-model="receiptSequenceNumber">
          </p>

          <p v-if="sigVerified1 && sigVerified2">👍</p>

          <details>
            <summary>Receipt JSON data</summary>
            <pre>{{ receipt }}</pre>
          </details>
        </section>
      </div>
    </div>
  </div>
</template>

<script>
import ethSigUtil from "eth-sig-util";
import truffleContract from "truffle-contract";
import ContractArtifact from "./contracts/ChannelCoin";
import { genMsgParams } from "./lib/channel-receipts";

const eqLowerCase = (a, b) => a.toLowerCase() === b.toLowerCase();

const ethSignTypedData = (signer, data) =>
  new Promise((resolve, reject) =>
    ethereum.sendAsync(
      {
        method: "eth_signTypedData_v3",
        params: [signer, data],
        from: signer
      },
      (err, result) => {
        if (err) {
          reject(err);
        }
        const { result: sig } = result;
        resolve(sig);
      }
    )
  );

export default {
  name: "app",
  data() {
    return {
      isLoaded: false,
      message: "",
      networkId: "",
      account: "",
      channelId: "0",
      contractAddress: "",
      actualTokenBalance: 0,
      contractBalance: 0,
      participant1: null,
      participant2: null,
      balance1: 0,
      balance2: 0,
      totalBalance: 0,
      sig1: "",
      sig2: "",
      receiptSequenceNumber: 0
    };
  },
  watch: {
    account: function() {
      if (!this.$options.contract) {
        return;
      }
      this.refreshBalance();
    },
    balance1: function() {
      this.balance2 = this.totalBalance - parseInt(this.balance1);
    },
    balance2: function() {
      this.balance1 = this.totalBalance - parseInt(this.balance2);
    }
  },
  computed: {
    you1: function() {
      return (
        this.account !== null &&
        this.participant1 !== null &&
        eqLowerCase(this.account, this.participant1)
      );
    },
    you2: function() {
      return (
        this.account !== null &&
        this.participant2 !== null &&
        eqLowerCase(this.account, this.participant2)
      );
    },
    sigVerified1: function() {
      if (!this.participant1 || !this.sig1) {
        return;
      }
      const { data } = genMsgParams(this.receipt);
      const signer = ethSigUtil.recoverTypedSignature({ data, sig: this.sig1 });
      return eqLowerCase(signer, this.participant1);
    },
    sigVerified2: function() {
      if (!this.participant2 || !this.sig2) {
        return;
      }
      const { data } = genMsgParams(this.receipt);
      const signer = ethSigUtil.recoverTypedSignature({ data, sig: this.sig2 });
      return eqLowerCase(signer, this.participant2);
    },
    receipt: function() {
      return {
        chainId: parseInt(this.networkId),
        verifyingContract: this.contractAddress,
        channelId: parseInt(this.channelId),
        receiptSequenceNumber: parseInt(this.receiptSequenceNumber),
        balance1: parseInt(this.balance1),
        balance2: parseInt(this.balance2),
        sig1: this.sig1,
        sig2: this.sig2
      };
    }
  },
  methods: {
    async onSubmit() {
      await this.refreshChannel();
    },
    async resetBalance() {
      await this.$options.contract.resetBalance({ from: this.account });
      await this.refreshBalance();
    },
    async refreshBalance() {
      const balance = await this.$options.contract.balanceOf(this.account);
      this.actualTokenBalance = balance.toString(10);
    },
    async refreshContractBalance() {
      const balance = await this.$options.contract.balanceOf(
        this.contractAddress
      );
      this.contractBalance = balance.toString(10);
    },
    async metamaskWatchToken() {
      ethereum.sendAsync(
        {
          method: "metamask_watchAsset",
          params: {
            type: "ERC20", // Initially only supports ERC20, but eventually more!
            options: {
              address: this.contractAddress,
              symbol: "CC",
              decimals: 0,
              image:
                "data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAIAAACQd1PeAAAADElEQVQI12NYe8EcAAPiAbVsCTevAAAAAElFTkSuQmCC"
            }
          },
          id: Math.round(Math.random() * 100000)
        },
        (err, addedBoolean) => {
          if (err) {
            console.error(err);
          }
        }
      );
    },
    async signReceipt() {
      const { data: msgParamsData } = genMsgParams(this.receipt);
      const data = JSON.stringify(msgParamsData);
      const signer = this.account;
      let sig;
      try {
        sig = await ethSignTypedData(signer, data);
      } catch (e) {
        console.error(e);
        return;
      }
      return sig;
    },
    async refreshChannel() {
      const rsp = await this.$options.contract.channels(this.channelId);
      const {
        participant1,
        participant2,
        openingBalance1,
        openingBalance2
      } = rsp;
      this.participant1 = participant1;
      this.participant2 = participant2;
      this.balance1 = openingBalance1.toNumber();
      this.balance2 = openingBalance2.toNumber();
      this.totalBalance = this.balance1 + this.balance2;
    },
    genSig1: async function() {
      if (!this.you1) {
        return;
      }
      this.sig1 = await this.signReceipt();
    },
    genSig2: async function() {
      if (!this.you2) {
        return;
      }
      this.sig2 = await this.signReceipt();
    }
  },
  async mounted() {
    window.vm = this;

    if (typeof window.ethereum === "undefined") {
      this.message = "No Ethereum provider (e.g. MetaMask) detected";
      return;
    }

    let accounts;
    try {
      accounts = await ethereum.enable();
    } catch (e) {
      console.error(e);
      this.message = "User rejected provider access";
      return;
    }
    const [account] = accounts;
    this.account = account;
    this.networkId = parseInt(ethereum.networkVersion);

    ethereum.on("accountsChanged", ([account]) => {
      this.account = account;
    });

    const Contract = truffleContract(ContractArtifact);
    Contract.setProvider(ethereum);

    let contract;
    if (this.networkId === 3) {
      // Ropsten
      contract = await Contract.at(
        "0x621be8c6e5811a0773de1492a550eab922fb72ff"
      );
    } else {
      contract = await Contract.deployed();
    }

    this.contractAddress = contract.address;
    this.$options.contract = contract;

    this.isLoaded = true;

    window.genMsgParams = genMsgParams;

    this.refreshBalance();
    this.refreshContractBalance();
  }
};
</script>

<style>
.cc-white {
  color: white;
}
.cc-green {
  color: #add037;
}

.cc-gray {
  color: #1e2122;
}

body,
html {
  margin: 0;
  padding: 0;
}

#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #1e2122;
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 6ex;
  margin: 0;
  background-color: #1e2122;
  padding: 10px 30px;
  text-align: right;
}

#container {
  padding: 0 30px;
  width: fit-content;
  margin: auto;
}

th,
td {
  padding: 5px;
  background-color: #fafafa;
}

button {
  height: 4ex;
  margin: 0 1ex;
}

.truncate {
  width: 20ex;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: inline-block;
}

.valid {
  color: green;
}

.invalid {
  color: red;
}
</style>
